@import '../sass/mixins/_mixins.sass';

.footer {
  background-color: $socket-d;
  padding: 4.8rem 0;
  @include lg {
    padding: 3.2rem 0;
  }
  .ml {
    margin-left: auto;
    @include lg {
      margin-left: 0;
    }
  }
  //&__info
  &__logo {
    max-width: 200px;
    margin-bottom: 2.8rem;
    @include lg {
      margin-bottom: 1.6rem;
    }
  }
  &__copy {
    @include font(1.4rem, 1.71rem, 500);
    @include lg {
      margin-bottom: 3.2rem;
    }
  }
  &__author {
    @include font(1.4rem, 1.71rem, 500);
    color: $text-d-60;
    margin-bottom: .4rem;
    a {
      @include transition;
      color: $white;
      &:hover {
        color: $text-d-60;
      }
    }
  }
  &__links {
    &--ml13 {
      margin-left: 13rem;
      @include lg {
        margin-left: 0;
      }
    }
    &-title {
      margin: 0 0 1.6rem;
      @include font(1.3rem, 1.23em, 600, 1px);
      color: $white;
      text-transform: uppercase;
    }
    &-items {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &-item {
      @include font(1.4rem, 1.71em, 500);
      color: $text-d-60;
      @include transition;
      &:hover, &.active {
        color: $white;
        transform: translateX(.4rem);
      }
      &:not(:last-child) {
        margin-bottom: .4rem;
      }
    }
  }
}
